.footerContentRight{
    width:250px;
    height: 135px;
    text-align: end;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 35px;
    /* display: flex;
    flex-flow: row;
    justify-content: space-between; */
}
.footerIcons ul{
    display: flex;
    justify-content: space-between;
}
.footerIcons ul li{
    list-style-type: none;
    width:44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
}
.footerIcons ul li:hover{
    background-color: #009999;
    color: white;
}